.construction-container{position:relative;display:flex;height:100vh;flex-direction:column;text-align:center;align-items:center}.construction-container .logo-container{display:flex;justify-content:center;flex-direction:column}.construction-container .logo-container .App-logo{font-family:var(--font-deco);color:var(--color-main);font-size:6em;align-items:center;opacity:0;margin-top:20px}.construction-container .logo-container .insta-icon{width:5em;position:relative;bottom:40px;opacity:0}.construction-container .logo-container .name{text-transform:uppercase;font-family:var(--font-base);position:relative;top:-20px;left:15px;letter-spacing:3px;color:#c8c8c8;opacity:0;margin:0}.construction-container .logo-container #logo-animation{text-align:center;-webkit-animation:fadein 3s 2s forwards;animation:fadein 3s 2s forwards}@-webkit-keyframes fadein{from{opacity:0}to{opacity:0.8}}@keyframes fadein{from{opacity:0}to{opacity:0.8}}.construction-container .construction-txt{text-transform:uppercase;letter-spacing:0.5em;opacity:0;color:#F9796E;font-weight:bold}.construction-container #construction-txt-animation{font-size:1.5em;text-align:center;-webkit-animation:fadein 2s 4s forwards;animation:fadein 2s 4s forwards}@keyframes fadein{from{opacity:0}to{opacity:0.8}}

.loader{height:40vh;position:relative;display:flex;width:300px}.loader_overlay{width:150px;height:150px;background:transparent;box-shadow:0px 0px 0px 1000px rgba(255,255,255,0.67),0px 0px 19px 0px rgba(0,0,0,0.16) inset;border-radius:100%;z-index:-1;position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.loader_cogs{z-index:-2;width:100px;height:100px;top:-120px !important;position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.loader_cogs__top{position:relative;width:100px;height:100px;-webkit-transform-origin:50px 50px;transform-origin:50px 50px;-webkit-animation:rotate 10s infinite linear;animation:rotate 10s infinite linear}.loader_cogs__top div:nth-of-type(1){-webkit-transform:rotate(30deg);transform:rotate(30deg)}.loader_cogs__top div:nth-of-type(2){-webkit-transform:rotate(60deg);transform:rotate(60deg)}.loader_cogs__top div:nth-of-type(3){-webkit-transform:rotate(90deg);transform:rotate(90deg)}.loader_cogs__top div.top_part{width:100px;border-radius:10px;position:absolute;height:100px;background:#f9796e}.loader_cogs__top div.top_hole{width:50px;height:50px;border-radius:100%;background:white;position:absolute;position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.loader_cogs__left{position:relative;width:80px;-webkit-transform:rotate(16deg);transform:rotate(16deg);top:28px;-webkit-transform-origin:40px 40px;transform-origin:40px 40px;animation:rotate_left 10s .1s infinite reverse linear;left:-24px;height:80px}.loader_cogs__left div:nth-of-type(1){-webkit-transform:rotate(30deg);transform:rotate(30deg)}.loader_cogs__left div:nth-of-type(2){-webkit-transform:rotate(60deg);transform:rotate(60deg)}.loader_cogs__left div:nth-of-type(3){-webkit-transform:rotate(90deg);transform:rotate(90deg)}.loader_cogs__left div.left_part{width:80px;border-radius:6px;position:absolute;height:80px;background:#c8c8c8}.loader_cogs__left div.left_hole{width:40px;height:40px;border-radius:100%;background:white;position:absolute;position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.loader_cogs__bottom{position:relative;width:60px;top:-65px;-webkit-transform-origin:30px 30px;transform-origin:30px 30px;-webkit-animation:rotate_left 10.2s .4s infinite linear;animation:rotate_left 10.2s .4s infinite linear;-webkit-transform:rotate(4deg);transform:rotate(4deg);left:79px;height:60px}.loader_cogs__bottom div:nth-of-type(1){-webkit-transform:rotate(30deg);transform:rotate(30deg)}.loader_cogs__bottom div:nth-of-type(2){-webkit-transform:rotate(60deg);transform:rotate(60deg)}.loader_cogs__bottom div:nth-of-type(3){-webkit-transform:rotate(90deg);transform:rotate(90deg)}.loader_cogs__bottom div.bottom_part{width:60px;border-radius:5px;position:absolute;height:60px;background:#f9b9b3}.loader_cogs__bottom div.bottom_hole{width:30px;height:30px;border-radius:100%;background:white;position:absolute;position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}@-webkit-keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes rotate_left{from{-webkit-transform:rotate(16deg);transform:rotate(16deg)}to{-webkit-transform:rotate(376deg);transform:rotate(376deg)}}@keyframes rotate_left{from{-webkit-transform:rotate(16deg);transform:rotate(16deg)}to{-webkit-transform:rotate(376deg);transform:rotate(376deg)}}@-webkit-keyframes rotate_right{from{-webkit-transform:rotate(4deg);transform:rotate(4deg)}to{-webkit-transform:rotate(364deg);transform:rotate(364deg)}}@keyframes rotate_right{from{-webkit-transform:rotate(4deg);transform:rotate(4deg)}to{-webkit-transform:rotate(364deg);transform:rotate(364deg)}}

